/*
** Bootstrap Addons
*/
/*
** Horizontal padding (gutters) for columns and rows
*/
/* columns
.c - columns
px-* - horizontal padding value
*/
.c-gutter-100 [class*=col-],
.row.c-gutter-100 > [class*=col-] {
  padding-right: 50px;
  padding-left: 50;
}

.c-gutter-80 [class*=col-],
.row.c-gutter-80 > [class*=col-] {
  padding-right: 40px;
  padding-left: 40;
}

