.row.c-gutter-25 > [class*=col-] {
  padding-right: 12px;
  padding-left: 12.5px;
}

.c-gutter-20 [class*=col-],
.row.c-gutter-20 > [class*=col-] {
  padding-right: 10px;
  padding-left: 10;
}

