  padding-right: 1px;
  padding-left: 1;
}

.c-gutter-1 [class*=col-],
.row.c-gutter-1 > [class*=col-] {
  padding-right: 0px;
  padding-left: 0.5px;
}

/*same as bootstrap .no-gutters class*/
.c-gutter-0 [class*=col-],
.row.c-gutter-0 > [class*=col-] {
  padding-right: 0px;
  padding-left: 0;
}

